/*!  */
.page-header {
  padding-top: 16px;
  padding-bottom: 13px;
  margin-bottom: 16px;
  background-color: $section-bg;

  @media (min-width: $screen-xl) {
    margin-bottom: 24px;
  }

  &__inner {
    @include container;
    display: flex;
    align-items: flex-end;
  }

}
